@import url(https://fonts.googleapis.com/css?family=Nunito);
.header{background-color:#91a88c;padding:0 30px;position:-webkit-sticky;position:sticky;display:flex;justify-content:space-between;align-items:center;height:60px;top:0}.header-link{list-style-type:none;color:black;font-weight:bold;text-decoration:none;padding:0 20px;height:60px;display:flex;align-items:center}.header-link:hover{background-color:#c3ceb4}.header-right{display:flex;align-items:center}.header-right>img:last-child{height:24px}.header-left{display:flex}.social-link{height:20px;padding:0 10px;cursor:pointer;display:block}.active{background-color:#c3ceb4}.userDropdownContent{display:none;position:absolute;background-color:#f9f9f9;min-width:160px;box-shadow:0px 8px 16px 0px rgba(0,0,0,0.2);padding:12px 16px;z-index:1}.userDropdown:hover .userDropdownContent{display:block}

.keyPointsButton{text-align:center;padding-bottom:40px}.homeImpact{text-align:center;height:250px;border-radius:15px;background-color:#EDF5E1;height:300px;margin:30px}.homeImpactTitle{padding-top:50px;padding-bottom:50px}.homeImpactStatNumber{font-size:50px}.homeImpactStats{display:flex;justify-content:center}.homeImpactStat{padding:10px}

.button{display:inline-block;margin-top:20px;text-decoration:none;font-family:'Nunito',sans-serif;font-weight:bold;color:black;font-size:14px;text-align:center;padding:12px 20px;background:#91a88c;transition:all 0.4s ease 0s;border-radius:50px}.button:hover{background:#c3ceb4;transition:all 0.2s ease}

.cardContainer{padding:20px;padding-left:35px;border-radius:15px;display:flex;width:80%;margin:20px auto;min-height:350px}.cardText{width:60%;align-self:center;text-align:justify}.cardText>p{padding:5px;width:75%}.cardImage{width:40%;padding-left:5em;align-self:center}.cardImage>img{width:100%}

.productContainer{padding:30px 20px;display:flex;flex-direction:column;border-radius:15px;background-color:rgba(195,206,180,0.6);margin:0 15px;max-width:400px;box-sizing:border-box}.productText{width:60%;align-self:center;text-align:center}.productIcon{width:40%;align-self:center;text-align:center}.productIcon>img{width:65%}.productPrice{font-weight:bold;margin-top:8%}.productTitle{margin:5% 0;font-size:17px}.productDescription{font-size:12px}

.cardTextContainer{display:flex;flex-direction:column;background-color:rgba(255,255,255,0.9);border-radius:15px;padding:40px;margin-left:15%;margin-right:15%;align-items:center}

.card{background-color:rgba(255,255,255,0.9);border-radius:15px;padding:30px 20px;margin:0 15px;font-size:10px;text-align:center;width:30%;height:auto;max-width:290px}.cardListItem{display:flex;padding:12px;font-family:'Nunito', bold;align-items:center;margin-left:30px}.cardListItemAmount{margin-left:20px;margin-right:10px;font-family:'Nunito', bold;font-size:15px}.cardListItemText{font-family:'Nunito', bold;font-size:15px}.cardTitle{padding-bottom:5px;border-bottom:1px solid black;margin-bottom:20px}

.keyPointsWrap{display:flex;justify-content:space-evenly;padding:40px 0}.keyPointsTitle{text-align:center;margin-top:15px}.keyPointElement{display:flex;flex-direction:column;max-width:200px;text-align:center}.keyPointElement>h3{padding:8px 0}

.buy{height:calc(100vh - 60px);align-items:center;width:100%;background:white}.buyHeader{text-align:center;padding:5% 0}.buyProducts{display:flex;justify-content:center;margin:auto;width:75%}

.sell{background:white}.sellHeader{text-align:center;padding:5% 0}.sellHeader>p{margin:15px}.sellSteps{display:flex;flex-direction:column;text-align:center}.sellStep{display:flex;flex-direction:column;max-width:50%;margin:auto;margin-bottom:50px}.sellStep>img{margin:10px}.sellStep>p{max-width:40%;margin:auto}.sellButton{padding-bottom:20px}

.footer{background-color:#c3ceb4;margin-top:70px}.footer-social{display:flex;justify-content:center;padding-top:25px;padding-bottom:15px}.footer-main{display:flex;justify-content:center;padding:20px}.footer-main-element{margin:0 20px}.footer-end{display:flex;justify-content:center;padding:10px}.footer-small{font-size:10px}

.project{width:70%;margin:auto}.projectTeamMembers{display:flex;justify-content:center}.projectTeamMember{width:175px;text-align:center;padding-right:25px}.projectTeamMemberPic{border:1px solid #ddd;border-radius:4px;padding:5px;max-width:100%;height:auto}.projectTitle{text-align:center;padding-top:25px;padding-bottom:25px}.projectMission{display:flex;flex-direction:column}.projectMissionText{width:80%;margin:auto;text-align:center;padding-bottom:50px;font-style:italic}.projectPartners{display:flex;flex-direction:column;text-align:center}.projectPartners>p{margin-bottom:50px}.projectPartnerPic{border:1px solid #ddd;border-radius:4px;padding:5px;max-width:100%;height:60%;margin:auto;margin-bottom:10px}

.contactHeader{background:url(/compensate/static/media/forest-4.741643f5.jpg) center/cover;text-align:center;color:white;padding:20px}.contactForm{display:flex;flex-direction:column;margin:auto}.contactForm>b>p{padding-bottom:5px}.contactForm>label{padding:5px}.contactTitle{padding:40px;text-align:center}.contactMain{display:flex;padding-bottom:40px;justify-content:center;width:60%;margin:auto}.contactMethods{margin:0 auto;display:flex;flex-direction:column}.contactImage{height:20px;padding-top:10px;padding-bottom:10px}

.sign{display:flex;justify-content:center;padding-top:50px;justify-content:space-evenly;max-width:70%;margin:auto}.signBox{max-width:500px;padding-right:50px;box-sizing:border-box;border-radius:15px;background-color:rgba(195,206,180,0.6);min-width:300px}.signBox>h2{padding-top:15px;padding-left:15px;max-width:70%}.signBox>h5{padding-top:5px;padding-left:15px}.signupForm{padding-top:20px;padding-left:15px;padding-bottom:20px}.signupStep{display:flex;flex-direction:column}.signupStep>input{margin-top:5px;margin-bottom:15px;width:80%;height:25px}.signupStep>p{max-width:70%}.passwordRequirements{padding-left:25px;padding-bottom:2px}.formError{font-size:15px;font-style:bold;color:red}

body{background-color:white;margin:0}h1,h2,h3,h4,h5,p,ul,li,a{font-family:'Nunito', bold}label{font-family:'Nunito', bold}*{margin:0;padding:0}p{font-size:14px}.mainRow{display:flex;justify-content:center;background:url(/compensate/static/media/forest-1.812266f6.jpg) center/cover;width:100%;height:calc(100vh - 60px);align-items:center}.rowContainer{justify-content:center;background:url(/compensate/static/media/forest-2.d611e140.jpg) center/cover}.row{padding:20px;display:flex}.text-tiny{font-size:10px;font-family:'Nunito', bold}.text-small{font-size:12px;font-family:'Nunito', bold}.text-normal{font-size:14px;font-family:'Nunito', bold}.text-big{font-size:16px;font-family:'Nunito', bold}input{box-sizing:border-box;border-radius:2px;border:0.4px solid black}

